 on sending you an email notification, but emails often don’t get read, therefore it is your responsibility to periodically check for changes to Goldee’s Terms. If you don’t agree with Goldee’s Terms, then you are not authorized to use Goldee.\nPrivacy\nYour privacy is important.  Goldee will not intentionally share your private information with other parties (i.e selling data to advertisers). Furthermore, we take security measures to protect against unauthorized access or alteration of your personal information. Goldee uses a AWS server to encrypt and secure your personal information from unauthorized access, use or disclosure with a Secure Sockets Layer (SSL) protocol.\n\nHowever, it’s important that you are aware that no data transmission on the internet can be guaranteed to be 100% secure. As a result, by agreeing to Goldee’s Terms, you acknowledge that there are security and privacy limitations inherent to the internet that are simply beyond our control and thus no data between you and this site can ever be guaranteed.\n\nIn addition, we want you to be aware of the following:\n\nWe collect and store data to improve our service and your experience. This includes: your location or address (whichever you provide us), profile and account information, login times and session lengths, clicks, and messages sent through the site, your IP address, browser type, and referring website address. Goldee does not knowingly collect personally identifiable information from anyone under the age of eighteen. We do not share this data with third-parties.\nWe use cookies, yum! Cookies help personalize your online experience and save you time by remembering information you have entered on previous visits to the site. A cookie is a text file that is placed on your hard disk by a web page server. Cookies are very common. They cannot be used to run programs or deliver viruses to your computer. Cookies are uniquely assigned to you and can only be read by a web server in the domain that issued the cookie to you. You have the ability to accept or decline cookies. Most web browsers automatically accept cookies, but you can usually personalize your browser to decline cookies if for some reason you wanted that.\nYou can use a third-party account to access your account. Goldee provides users with the ability to connect to their account through third-party accounts (i.e “Login with Facebook”). By connecting your Goldee account to a third-party account, you acknowledge and agree that you are consenting to the continuous release of your information to this third-party site in accordance with your privacy settings on the third-party site. If you do not want information about you to be shared in this manner, do not use this feature. We cannot control what third parties do with your data if you choose to connect your account to them. You may disconnect your account from a third party account at any time.\nGoldee will email you. From time to time, Goldee may contact you via email for the purpose of providing announcements, promotional offers, alerts, confirmations, verifications, surveys, and other general communication. In order to improve our services, we track how often these emails are opened and what links are clicked within them. We understand no one likes spam and we strive to be respectful and only email you when it’s important. If you would like to stop receiving emails, you may opt out of communication anytime by replying “unsubscribe” to the email.\nGoldee contains links to other sites. This should be obvious, but just to be clear: we are not responsible for the content or privacy practices of other sites.\nSafety\nMost people are well intentioned, and the the vast majority of human-human interactions are safe. However, there are some people in the world that want to do harm, so it’s important to always proceed with caution when interacting with strangers.\n\nWe want you to be safe when using Goldee. Unfortunately, Goldee cannot guarantee your wellbeing. Your personal safety is always your responsibility.\n\nHere are some tips that can help you stay safe when meeting people you found online:\n\nGoogle the person you’re meeting. It’s easy to do and never hurts to verify information you know about the person or see if you find something fishy.\nKeep your address private. Goldee does not share your location or address information with other users. It’s used to filter results based on proximity. No one knows exactly where you are unless you tell them.\nMeet in a public place. If you’re meeting someone for the first time, insist on a public place such as a coffee shop, library, or busy shopping center and, if possible, bring a friend with you.\nTell someone what you’re doing.  Always let a friend or family member know where you are going and who you are meeting with. Bring a fully charged cell phone in case you run into trouble. Try Circleof6, bSafe, and Ulzi, these apps were designed to provide safety features when meeting with strangers.\nConsider getting a Google Voice number. Google Voice is a free service that allows you to select a number that you can give out to people you don’t know. Calls to that number are forwarded to your phone. This provides you with an additional layer of privacy when giving out your number. You can also block numbers, and the caller will receive a “number not in service” message.\nCall or video conference with the person ahead of time. Hearing someone’s voice can help you discern if the person is who they say they are.\n\nTrust your instincts. If something just doesn’t feel right about the person you met, chances are you’re on to something.\n\nGoldee is intended to help make the world a better and more connected place. But, we can’t pretend there aren’t bad people in the world. By taking precautions, we can help mitigate the risk involved in meeting new people.\n\nFurthermore, the safety of Goldee is largely dependent on the users of Goldee. Therefore in order to use Goldee you (and all other users) must agree to:\n\nBe good. Never post content that is purposely misleading, false, hateful, threatening, pornographic, or incites violence. Never use Goldee to do or post anything unlawful, misleading, malicious, or discriminatory. Never use Goldee to stalk, troll, bully, intimidate, or harass any user. Do not use Goldee in any way that violates someone else’s rights. Never abuse Goldee’s reporting system.\nNo funny business: Never upload viruses, malicious code, or do anything that could impair the proper working or appearance of Goldee.\nNo stealing: Never collect or copy users' information or content either by using automated means (bots, robots, spiders, or scrapers) or manual means (handcopying).\nReal accounts: Never create more than one personal account. Never solicit login information, or access an account belonging to someone else without their explicit permission. Never create an account for anyone other than yourself without explicit permission. Never create a new account without Goldee’s explicit permission if yours has been deleted by Goldee. Never purchase or sell Goldee accounts. Never create a bot account.\nNo rapists or pediphiles. Do not use Goldee if you are a convicted sex offender.\nBe responsible. It is your responsibility to use caution and common sense when interacting with others you meet on Goldee. Read and follow our safety page (GoGoldee.com/safety) for information on protecting yourself.  Please help make Goldee a safe space by reporting any content that violates Goldee’s Terms.\nProhibited Goods, Services and Content\nHere’s a list of goods, services, and content prohibited on Goldee:\n\nIllegal stuff. No good, service, or content that violates the law or legal rights of others, such as; child pornography, bestiality, offers or solicitation of illegal prostitution.\nCommerce. Goldee is not a marketplace for selling stuff. No affiliate marketing, network or multi-level marketing; pyramid or ponzi schemes. No postings or emails with the primary purpose of driving traffic to a website or promoting unsolicited products or services. No pet sales (rehoming with a small adoption fee may be permissible)\nWeapons. No guns and firearm components, bb guns, stun guns, tasers, pepper spray, bow and arrows, spear guns, pellet guns, ammunition, gunpowder, explosives, clips, cartridges, etc. or materials intended for the creation, alteration or sale of weapons. Basically any good, service, or content created or intended to harm someone\nStolen stuff. No stolen items, identities, and properties. This includes items with serial number removed or altered, tools or information intended for stealing, ID cards, licenses, government documents, etc.\nControlled stuff. No prescription drugs, controlled substances, alcohol, tobacco, nicotine, nicotine products, medical devices and other related items, recalled items, human body parts/fluids, hazardous materials, endangered and protected species and any parts thereof such as ivory or fur.\nGovernment stuff. No SNAP, EBT or WIC goods and vouchers, food stamps, nor any governmental assistance. No US military items, no demilitarized in accordance with the Defense Department policy, police items (uniforms, badges, cars, etc.).\nTicket and lottery stuff. No tickets, gift cards, lottery tickets, raffle tickets, pirated tickets, sweepstakes entries, slot machines, gambling items, etc.\nFake stuff. No false, deceptive, misleading or fraudulent content. No counterfeit, replica, or pirated items.\nGross stuff: No unsanitized bedding, linens, mattresses, and clothing.\nBad stuff: No offensive, defamatory, malicious or threatening postings or emails. No spam, overposted, or non-local content.\nDisputes\nGoldee is a neutral platform. All interactions on Goldee are between the parties using Goldee. Goldee is not involved or liable in any way.\nTermination\nIf you violate the letter or spirit of Goldee’s Terms, or otherwise create risk or possible legal exposure for Goldee, we can, and probably will, delete your account. We also have the right to block your IP address from using Goldee. We have the irrevocable right to remove or delete content and/or accounts at any time. We sincerely hope this never happens.\nContact Information\nIf you have something to say to Goldee, say it! You can always connect with Goldee at support@gogoldee.com.\n\n\nThank you,\nGoldee\n\n\n\n      ")])}]
